<template>
	<div class="home_abnormal">
		<div class="home_abnormal-item">
			<p class="home_title">人员考勤日统计</p>
			<div class="day" id="statistics-day-echarts"></div>
		</div>
		<div class="home_abnormal-item">
			<p class="home_title">人员考勤月统计</p>
			<div class="month" id="statistics-mouth-echarts">

			</div>
		</div>
		<div class="home_abnormal-item">
			<p class="home_title">人员考勤年统计</p>
			<div class="year" id="statistics-year-echarts">

			</div>
		</div>
	</div>
</template>

<script>
	var echarts = require('echarts');
	export default {
    name: 'Attendance',
    props: ['dayInfo','monthInfo','yearInfo'],
    watch: {
      // 人员考勤日统计数据
      dayInfo (val) {
        // 如果从后台返回数据成功开始渲染数据
        if (this.dayInfo && this.dayInfo.ret == 200) {
          // 日echarts图
          var dayEchartsRight = echarts.init(document.getElementById("statistics-day-echarts"));
          this.dayEchartsRightData = {
            time: val.xAxis,
            shangganglv: val.practicalVal,
            shanggangren: val.practical,
            max: val.max,
            interval: val.interval,
            maxRight: this.max(val.practicalVal)
          }
          this.renderEcharts(dayEchartsRight, this.dayEchartsRightData)
        }
      },
      // 人员考勤月统计数据
      monthInfo (val) {
        if (this.monthInfo && this.monthInfo.ret == 200) {
          // 月echarts图
          var monthEchartsRight = echarts.init(document.getElementById("statistics-mouth-echarts"));
          this.monthEchartsRightData = {
            time: val.xAxis,
            shangganglv: val.practicalVal,
            shanggangren: val.practical,
            max: val.max,
            interval: val.interval,
            maxRight: this.max(val.practicalVal)
          }
          this.renderEcharts(monthEchartsRight, this.monthEchartsRightData)
        }
      },
      // 人员考勤年统计数据
      yearInfo (val) {
        if (this.yearInfo && this.yearInfo.ret == 200) {
          // 年echarts图
          var yearEchartsRight = echarts.init(document.getElementById("statistics-year-echarts"));
          this.yearEchartsRightData = {
            time: val.xAxis,
            shangganglv: val.practicalVal,
            shanggangren: val.practical,
            max: val.max,
            interval: val.interval,
            maxRight: this.max(val.practicalVal)
          }
          this.renderEcharts(yearEchartsRight, this.yearEchartsRightData)
        }
      }
    },
	  data () {
      return {
        arr: [
          {
            name: '考勤率',
            icon: 'image://data:image/png;base64,'
          },
          '考勤人数（人）'
        ],

        // 日统计
        dayEchartsRightData: {
          time: '',
          shangganglv: '',
          shanggangren: '',
          max: '',
          interval: '',
          maxRight: ''
        },

        // 月统计
        monthEchartsRightData:{
          time: '',
          shangganglv: '',
          shanggangren: '',
          max: '',
          interval: '',
          maxRight: ''
        },

        //年统计
        yearEchartsRightData:{
          time: '',
          shangganglv: '',
          shanggangren: '',
          max: '',
          interval: '',
          maxRight: ''
        }
      }
    },
	  methods:{
      renderEcharts(myChart,data){
        var option = {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          legend: {
            data:this.arr,
            show:true,
            itemWidth:20,  //设置图例的宽高
            itemHeight:10,
            textStyle: {
              fontWeight: "normal",              //标题颜色
              color: "#fff"
            }
          },
          grid:{
            x:30,
            y:30,
            x2:42,
            y2:40
          },

          xAxis: [
            {
              type: 'category',
              data:data.time,
              // data:this.dayXTime,
              axisLine: {  // 控制x轴线的样式
                lineStyle: {
                  type: 'solid',
                  color: '#56affb',
                  width:'1' }
              },
              axisPointer: {
                type: 'shadow'
              },
              axisLabel : {
                formatter: '{value}',
                textStyle: {
                  color: '#2DD7EC'
                }
              },
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '',
              // min: 0,
              max: data.max,
              interval: data.interval,
              axisLine: {  // 控制y轴线的样式
                lineStyle: {
                  type: 'solid',
                  color: '#56AFFB',
                  width:'1' }
              },
              axisLabel: {
                formatter: '{value} ',
                textStyle: {
                  color: '#556BA2'
                }
              },
              splitLine: { //网格线
                show: false,
                lineStyle:{
                  color: ['#56AFFB'],
                  width: 1,
                  type: 'dotted'
                }
              }
            },
            {
              type: 'value',
              name: '',
              min: 0,
              max: data.maxRight,
              smooth:true,
              axisLabel: {
                formatter: '{value} %',
                textStyle: {
                  color: '#556BA2'
                }
              },
              axisLine: {  // 控制y轴线%的样式
                lineStyle: {
                  type: 'solid',
                  color: '#23BBEC',
                  width:'1' }
              },
              splitLine: {
                show: false,
                lineStyle:{
                  color: ['#56AFFB'],
                  width: 1,
                  type: 'dotted'
                }
              }
            },
          ],
          series: [
            {
              name: '考勤率',
              icon:'rect',
              type: 'line',
              yAxisIndex: 1,
              smooth: true,
              symbol: 'none',
              data:data.shangganglv,
              itemStyle: {
                normal: {
                  color: 'rgba(255,235,12, 1)',//这里修改了柱子的颜色以及透明度，注意如果要修改透明度，一定要配置在itemStyle里面，直接写在外面不起作用。
                  lineStyle: {        // 系列级个性化折线样式
                    width: 3,
                    type: 'solid',
                  }
                }
              }
            },
            {
              name: '考勤人数（人）',
              type: 'bar',
              radius: '10%',
              data: data.shanggangren,
              itemStyle: {
                normal: {
                  barBorderRadius: [4, 4, 4, 4],
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#2cd3ec'},
                      {offset: 0.5, color: '#32a6e9'},
                      {offset: 1, color: '#387de6'}
                    ])
                }
              },
              barWidth: 8, // 控制柱子的宽度
              barGap:'50%', // 控制柱子的间隔
            }
          ]
        };
        myChart.setOption(option, true);

      },
      // 获取数组中最大值
      max (arr) {
        var max = parseInt(arr[0])
        for(var i=1;i<arr.length;i++){
          if(parseInt(arr[i])>max){
            max = arr[i]
          }
        }
        return max
      }
	  },
}
</script>

<style lang="scss" scoped>
  @import '~@/assets/css/home';
</style>
