<template>
	<div style="height:100%">
    <div class="checking-text" style="height:35%">
      <p class="home_title">人员考勤信息</p>
      <div class="checktest-wrap">
        <div class="total-pro">
          <img src="../assets/images/B.png" alt="">
          <div>
            <p>{{info.totalNum}}</p>
            <span>计件总人数</span>
          </div>
        </div>
        <div class="single-pro">
          <ul class="top">
            <li>
              <p v-text="info.userRecordNum"></p>
              <span>考勤人数(人)</span>
            </li>
            <li>
              <p v-text="Math.round(info.recordTime*100)/100"></p>
              <span>考勤时间</span>
            </li>
            <li>
              <div  id="shangganglv" class="lv"></div>
              <span>考勤率</span>
            </li>

            <li>
              <p v-text="info.planNum"></p>
              <span>计划考勤人数(人)</span>
            </li>
            <li>
              <div  id="kaoqinlv" class="lv"></div>
              <span>计划考勤率(%)</span>
            </li>
          </ul>
          <ul class="bottom" style="margin-top:25px;">
            <li>
              <p v-text="info.onWorkNum"></p>
              <span>在岗人数(人)</span>
            </li>
            <li>
              <p v-text="Math.round(info.onWorkTime*100)/100"></p>
              <span>有效在岗时间(h)</span>
            </li>
            <li>
              <div  id="zaiganglv" class="lv"></div>
              <span>在岗率(%)</span>
            </li>
            <li>
              <p v-text="info.validNum"></p>
              <span>有效在岗人数(人)</span>
            </li>
            <li>
              <div  id="jihuakaoqinlv" class="lv"></div>
              <span>有效上岗率(%)</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="checking-list" style="height:64%">
      <p class="home_title">人员考勤列表</p>
      <!--search-->
      <div class="checking-search">
        <input class="searchname" type="text" placeholder="姓名" ref="workname">
        <input class="searchtext" type="text" placeholder="工号" ref="worknum">
        <div class="gosearch" @click="getProInfo">搜索</div>
        <div class="clear" @click="clearProInfo">清空</div>
        <div class="kaoqin">考勤</div>
      </div>
      <!-- <div class="checklist-wrap"> -->
        <!--Echarts-->
        <div id="echarts-time"></div>
        <!--平面图-->
      <!-- </div> -->
    </div>
	</div>
</template>

<script>
 	var echarts = require('echarts');
	export default {
   name: 'checking',
   props: ['info','hourEchartsDataBetween'],
   watch: {
     info () {
       // 如果有基本信息，渲染中间顶部的4个echarts图
      if (this.info) {
        this.renderEchartsCircle()
      }
     },
     hourEchartsDataBetween () {
       if (this.hourEchartsDataBetween) {
        /*1小时考勤*/
        this.hourEchartsData =  {
          // secData:['1分钟','2分钟','3分钟','4分钟','5分钟','6分钟','7分钟','8分钟','9分钟','10分钟','11分钟','12分钟','13分钟','14分钟','15分钟','16分钟','17分钟','18分钟','19分钟','20分钟','21分钟','22分钟','23分钟','24分钟','25分钟','26分钟','27分钟','28分钟','29分钟','30分钟','31分钟','32分钟','33分钟','34分钟','35分钟','36分钟','37分钟','38分钟','39分钟','40分钟','41分钟','42分钟','43分钟','44分钟','45分钟','46分钟','47分钟','48分钟','49分钟','50分钟','51分钟','52分钟','53分钟','54分钟','55分钟','56分钟','57分钟','58分钟','59分钟','60分钟'],
          secData:this.hourEchartsDataBetween.xAxis, //X轴
          numData:this.hourEchartsDataBetween.dateStr, //在岗人数
          lvData:this.hourEchartsDataBetween.dateStrval,//在岗率
          max: this.hourEchartsDataBetween.max,
          interval: this.hourEchartsDataBetween.interval
        };
        var hourEcharts = echarts.init(document.getElementById('echarts-time'));
        this.renderEchartsHour(hourEcharts,this.hourEchartsData)
      }
     }
   },
	 data () {
     return {
      arr:[
        {name:'上岗率',icon:'image://data:image/png;base64,\n'},     //图例的样式设置
        {name:'在岗人数',icon:'image://data:image/png;base64,'},
      ],
      annularChartDataOne:{},  //有效上岗率
      annularChartDataTwo:{},   //考勤率
      annularChartDataThree:{},  //计划考勤率
      annularChartDataour:{},    //在岗率
      hourEchartsData:{
        secData: '',
        numData: '',
        lvData: '',
        max: '',
        interval: ''
      }
     }
	 },
	 components: {
	 },
	 computed: {
	 },
   created () {

   },
	mounted() {
  },
  methods: {
    renderEchartsCircle () {
      /*四个效率图*/
      var shangganglvEcharts =  echarts.init(document.getElementById('shangganglv'));
      var kaoqinlvEcharts = echarts.init(document.getElementById('kaoqinlv'));
      var jihuakaoqinlvEcharts = echarts.init(document.getElementById('jihuakaoqinlv'));
      var zaiganglvEcharts = echarts.init(document.getElementById('zaiganglv'));
      this.annularChartDataOne = {e:this.info.recordRate}   //考勤上岗率
      this.annularChartDataTwo = {e:this.info.workPlanRate}    //计划考勤率
      this.annularChartDataThree = {e:this.info.validRate}   //有效考勤率
      this.annularChartDataour = {e:this.info.onWorkRate}     //在岗率
      this.renderEcharts(shangganglvEcharts,this.annularChartDataOne) //考勤上岗率
      this.renderEcharts(kaoqinlvEcharts,this.annularChartDataTwo)  //计划考勤率
      this.renderEcharts(jihuakaoqinlvEcharts,this.annularChartDataThree) //有效考勤率
      this.renderEcharts(zaiganglvEcharts,this.annularChartDataour) //在岗率
    },
	   //四个考勤率
    renderEcharts(myChart,data){
      var option = {
        title: {
          show: true,
          text: data.e,
          x: 'center',
          y: 'center',
          textStyle: {
            fontSize: '22',
            color: '#0182F2',
            fontWeight: 'normal'
          }
        },

        color: ["rgba(51,86,173,0.3)"],
        legend: {
          orient: 'vertical',
          x: 'left',
          show: true
        },
        series:
          {
            name: '',
            type: 'pie',
            radius: ['80%', '100%'],
            center: ["50%", "50%"],
            avoidLabelOverlap: true,
            hoverAnimation: false,
            itemStyle:{
              barBorderRadius:'50%'
            },
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: data.e,

                itemStyle: {

                  color: {
                    type: 'linear',
                    colorStops: [
                      {
                        offset: 0.2, color: '#0AA3E6' // 0% 处的颜色
                      },
                      {
                        offset: 1, color: '#9D22E6' // 100% 处的颜色
                      }],
                    globalCoord: true // 缺省为 false
                  }
                }
              },
              {value: 100 - data.e}
            ]
          }

      };
      myChart.setOption(option, true);
    },
    //1小时考勤统计
    renderEchartsHour(myChartH,data){
      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data:this.arr,
          show:true,
          top: 10,
          x:680,    //图例水平位置
          itemWidth:20,  //设置图例的宽高
          itemHeight:10,
          textStyle: {
            fontWeight: "normal",              //标题颜色
            color: "#fff"
          }
        },
         grid:{    //调整坐标轴
           x:30,
           y:30,
           x2:52,
           y2:60
         },
         dataZoom:[{
          show:false,
           start:0,
           end:120,
           zoomLock:true,   //禁止鼠标缩放
         },
           {
             type:'inside',
             start:0,
             end:60,
             zoomLock:true,
           }],
        xAxis: [
          {
            type: 'category',
            data: data.secData,
            axisLine: {  // 控制x轴线的样式
              lineStyle: {
                type: 'solid',
                color: '#56AFFB',
                width:'1' }
            },
            axisPointer: {
              type: 'shadow'
            },
            axisLabel : {
              formatter: '{value}',
              textStyle: {
                color: '#2DD7EC'
              }
            },
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            // min: 0,
            max: data.max,
            interval: data.interval,
            axisLine: {  // 控制y轴线的样式
              lineStyle: {
                type: 'solid',
                color: '#23BBEC',
                width:'1' }
            },
            axisLabel: {
              formatter: '{value} ',
              textStyle: {
                color: '#3AD6E1'
              }
            },
            splitLine: { //网格线
              show: false,
              lineStyle:{
                color: ['#2DD7EC'],
                width: 1,    //
                type: 'dotted'
              }
            }
          },
          {
            type: 'value',
            name: '',
            min: 0,
            max: 100,
            interval: 20,
            smooth:true,
            axisLabel: {
              formatter: '{value} %',
              textStyle: {
                color: '#3AD6E1'
              }
            },
            axisLine: {  // 控制y轴线%的样式
              lineStyle: {
                type: 'solid',
                color: '#23BBEC',
                width:'1' }
            },
            splitLine: {
              show: false,
              lineStyle:{
                color: ['#23BBEC'],
                width: 1,
                type: 'dotted'
              }
            }
          }
        ],
        series: [
          {
            name:'在岗人数',
            type:'bar',
            data:data.numData,
            itemStyle: {
              normal: {
                barBorderRadius:[0, 0, 20, 20],   //柱状图加圆角
                barBorderRadius: 20,  //圆柱体效果边界圆角
                color: new echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                    {offset: 0, color: '#13F192'},
                    {offset: 1, color: '#478FEC'}
                  ]
                )
              }
            },

            barWidth: 6, // 控制柱子的宽度
            barGap:'50%' // 控制柱子的间隔
          },
          {
            name:'上岗率',
            type:'line',
            yAxisIndex: 1,
            symbol: "none",
            smooth:true,
            data:data.lvData,
            itemStyle: {
              normal: {
                lineStyle:{
                  width:5      //曲线粗细样式
                },
                color: 'rgba(13,204,233, 1)'//颜色以及透明度，注意如果要修改透明度，一定要配置在itemStyle里面，直接写在外面不起作用。
              }
            }
          }
        ]
      };
        myChartH.setOption(option, true);
    },
    //搜索
    getProInfo(){
      console.log(this.$refs.workname.value)
    },
    clearProInfo(){
      this.$refs.workname.value = ""
      this.$refs.worknum.value = ""
    }



  },
 }
</script>

<style lang="scss" scoped>
  @import '~@/assets/css/home';
  .checking-text{
    .checktest-wrap{
      .single-pro{
        &>ul>li:nth-child(1) p{
          background: url("../assets/images/C1.png") no-repeat;
          width:131px;height:107px;
          // background-size: contain;
        }
        &>ul>li:nth-child(2) p{
          background: url("../assets/images/C2.png") no-repeat;
          width:131px;height:107px;
          // background-size: contain;
        }
        &>ul>li:nth-child(4) p{
          background: url("../assets/images/C3.png") no-repeat;
          width:131px;height:107px;
          // background-size: contain;
        }
      }
    }
  }
  .checking-list{
    .checklist-wrap{
      .search-list{
        .search-map{
          background:url("../assets/images/planar-graph.png") no-repeat;
          background-size: 100% 100%;
          background-position-x: 18px;
        }
      }
    }
  }
</style>
